// @import './fonts';
@import './variables';
@import 'libs/normalize';

@import 'components/all';

body {
  background-color: $background;
}

.wrapper {
  width: calc(100vw - 40px);
  
  height: 100%;
  background-color: #fff;
  margin: 20px auto;

  border-radius: 10px;
  max-width: 1400px;

  @media(min-width: 645px){
    width: calc(100vw - 100px);
    margin: 50px auto;
  }
}

.content {
  padding: 40px 0;

  &__error-info{
    text-align: center;
     margin: 60px auto;
     width: 480px;
     h2 {
      font-style: 32px;
      font-weight: 700;
      margin-bottom: 20px;
     }
     p {
      font-style: 22px;
     }
  }

  &__title {
    margin-top: 35px;
    margin-bottom: 40px;
    font-weight: 800;
    font-size: 40px;

  }

  &__items {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    grid-column-gap: 11px;

    @media(min-width: 730px){
      grid-template-columns: repeat(2, 1fr);

  }

    @media(min-width: 1100px){
        grid-template-columns: repeat(3, 1fr);

    }
    @media(min-width: 1400px){
      grid-template-columns: repeat(4, 1fr);

    }
  }

  &__top {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: baseline;

    .categories{
      margin-bottom: 30px;
      width:100%;
      overflow: auto;
      ul{
        width: 785px;
      }
      @media(min-width: 730px){
        // width: 500px;
      }
      @media(min-width: 1292px){
        width: 820px;
        overflow: hidden;
      }
    }
    .sort{
      b{
        display: none;
      }
   }

    

    @media(min-width: 1260px){
      flex-direction: row;

      .categories{
        margin-top: 0;
      }
      .sort{
         b{
           display: block;
         }
      }
    }
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    @media(min-width:550px){
      flex-direction: row;
      
    }
  }

  
  

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;

    @media(min-width: 700px){
    
      flex-direction: row;
    }



    &-img {
      display: flex;
      align-items: center;
      margin-bottom: 15px;
      @media(min-width: 700px){
         margin-bottom: 0;
      }

      img {
        
        @media(min-width: 700px){
          width: 80px;
          height: 80px;
       }
      }
    }

    &-info {
      width: 170px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-bottom: 10px;
      @media(min-width:700px){
        align-items: flex-start;

      }

      @media(min-width:1054px){
        width: 220px;
        margin-bottom: 0;

      }
      

      h3 {
        font-weight: bold;
        font-size: 22px;
        text-align: center;
        line-height: 27px;
        letter-spacing: 0.01em;
        margin-bottom: 10px;
        @media(min-width:700px){
          text-align: left;
        }
        
       
      }

      p {
        font-size: 20px;
        text-align: center;
        color: #8d8d8d;
        margin-bottom: 10px;
    
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
      width: 100px;

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }

        &:disabled {
          opacity: 0.4;
          border-color: #787878;

          &:active{
            transform: none;
          }

          &:hover{
            background-color: transparent;
            svg path {
              fill: #787878
            }
          }

          svg path {
            fill: #787878
          }
        }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      position: absolute;
      top: 30px;
      right: 30px;
      @media(min-width: 700px){
        position: inherit;
        top: 0;
      }

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11.5px;
          height: 11.5px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }
  .content__items{
    display: block;
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      align-items: center;
      @media(min-width: 700px){
        flex-direction: row;
        
      }
      

      span {
        font-size: 22px;
        margin-bottom: 10px;
        @media(min-width: 700px){
          margin-bottom: 0;          
        }

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
      flex-direction: column-reverse;
      align-items: center;
      @media(min-width: 550px){
          flex-direction: row;
      }


      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

       


        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
        margin-bottom: 20px;
        @media(min-width: 550px){
          margin-bottom: 0;
        }
      }
    }
  }

  &--empty {
    padding: 20px;
    margin: 80px auto;
    text-align: center;

    h2 {
      line-height: 1.4;
      font-size: 36px;
      font-weight: 700;
      margin-bottom: 10px;
    }

    p {
      font-size: 20px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 270px;
      margin: 45px auto 60px;
      @media(min-width: 550px){
        width: 300px;
      }
      
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
