.root{
    text-align: center;
     li {
         display: inline-block;

         a{
             display: inline-block;
             color: #fe5f1e;
             text-align: center;
             width: 45px;
             height: 45px;
             line-height: 42px;
             border: 1px solid #fe5f1e;
             border-radius:30px ;
             margin-right: 10px;
             cursor: pointer;
             &:hover{
                background-color: #fe5f1e;
                   color: #fff;
               
            }
         }
     }

     :global{
         .selected{
             a{
                 background-color: #fe5f1e;
                 color: #fff;
             }
         }
     }
}